      }
    };
  },methods: {
    async idFindButton(){
      try {
        let data = await axios.post("http://localhost:3000/find",{
           UserId: this.form.id,
           UserPw: this.form.password
         });
         console.log(this.form.id)
         //console.log(data.data[0].UserId)
         console.log(data)
         if(this.form.id === data.data[0].UserId && this.form.password === data.data[0].UserPw){
          alert("로그인");
         }
         

         this.$router.push("/mian");
 
         
       } catch (err) {
        alert("로그인 실패");
       }
    },
     async pwFindButton(){
